.app-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  padding-bottom: 0.25rem;
  position: relative;
}

.app-header-underline {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  background: #000000;
  animation: trace-line 1s ease-in-out 1s forwards;
}

@keyframes trace-line {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

.logo {
  display: flex;
  gap: 1rem;
}

.logo img {
  display: block;
  height: 60px;
  width: auto;
}

.logo h1 {
  display: none;
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: 2px;
}

@media screen and (min-width: 768px) {
  .logo h1 {
    display: block;
  }
}

.app-nav ul, .app-nav li {
  padding: 0;
  margin: 0;
}

.app-nav ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.app-nav a {
  text-decoration: none;
  color: #000000;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 2px;
  padding: 0.5rem 1rem;
  transition: all 0.1s ease-in-out;
}

.nav-item:hover a {
  text-shadow: 0 1px 2px #3f4d5b;
}
