@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Inknut+Antiqua:wght@300&family=Noto+Sans:ital,wght@1,300&display=swap');

* {
  user-select: none;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 768px;
  margin: auto;
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin: 1rem 0;
}

a {
  text-decoration: none;
  color: #000000;
}

.main-header {
  font-family: 'Century Gothic', sans-serif;
  font-size: 2rem;
  letter-spacing: 1px;
  margin: 1rem 0;
}

.subtitle {
  font-family: 'Century Gothic', sans-serif;
  font-size: 1.5rem;
  letter-spacing: 1px;
  margin: 1rem 0;
}
