.main-header.civ-name {
  font-family: 'Great Vibes', sans-serif;
  font-size: 3rem;
}

.random-civ-flag {
  display: block;
  width: 128px;
  height: 96px;
  cursor: pointer;
  transition: all 75ms ease-in-out;
}

.random-civ-flag.small {
  width: 96px;
  height: 72px;
}

.random-civ-flag.selected {
  padding: 2px;
  border: 2px solid #000;
}

.random-civ-flag-caption {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-style: italic;
  color: #2a202d;
  margin: 0.5rem 0;
}

.random-civ-flag-caption.selected {
  font-weight: bold;
}

.selected {
  filter: brightness(1.2);
}

.random-civ-list {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.5rem;
  max-width: 768px;
}

.random-civ-list.civs {
  flex-direction: row;
  max-height: 0;
  overflow: hidden;
  padding-bottom: 0;
  transition: all 0.5s ease-in-out;
}

.random-civ-list.civs.open {
  max-height: 500px;
  padding-bottom: 1rem;
}

.random-civ-list.cultures {
  flex-direction: column;
  text-align: center;
}

ul.random-civ-list, ul.random-civ-list li {
  padding: 0;
  margin: 0;
}

.selections-header {
  background: url('../data/assets/headerbar.png');
  background-size: cover;
  background-position: center;
  width: 100%;
  margin: 1rem 0;
  font-family: 'Inknut Antiqua', serif;
  font-size: 1.5rem;
  font-weight: 300;
  color: #ffffff;
  text-align: center;
}

.culture-header {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 2rem;
  font-weight: 400;
  box-shadow: 0 0 2px #00000030;
  margin: 1rem 0;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}

.culture-header:hover {
  text-shadow: 0 1px 2px #3f4d5b;
}

.hint {
  font-family: 'Noto Sans', sans-serif;
  font-style: italic;
  color: #2a202d;
}
